@import "reset";
@import "fonts";
@import "mixins";
@import "vars";
@import "generated/sprite-svg";
:root {
  font-family: $font-primary;
  line-height: 1.6;
  * {
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    -webkit-overflow-scrolling: touch;
    box-sizing: border-box;
  }
}

body,
html {
  width: 100%;
  height: 100%;
}

.wrapper {
  width: 100%;
  min-height: 100%;
  overflow: hidden;
  position: relative;
  font-family: $font-primary;
  @media only screen and (max-width: 768px) {
    padding-top: 50px;
  }
  .icon {
    transition: all .2s ease;
  }
  a {
    color: inherit;
    transition: color 0.2s ease;
    &:hover {
      text-decoration: none;
    }
  }
}

.container {
  max-width: 1920px;
  margin: 0 auto;
}

.bold-text {
  font-weight: bold;
}

.account__avatar {
  width: 37px;
  height: 37px;
  margin-right: 13px;
  border-radius: 50%;
  overflow: hidden;
  img {
    display: block;
    width: 100%;
  }
}

// HEADER
.header {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  min-height: 74px;
  padding-left: 30px;
  padding-right: 20px;
  background-color: #28292d;
  z-index: 100;
  @media only screen and (max-width: 768px) {
    position: fixed;
    top: 0;
    left: 0;
    min-height: 50px;
    padding: 5px 2% 0px 3%;
  }
}

.header__left {
  display: flex;
  align-items: baseline;
  padding-bottom: 5px;
  @media only screen and (max-width: 768px) {
    flex-direction: column;
  }
}

.header__logo {
  margin-right: 21px;
}

.header__logo-link {
  width: 121px;
  img {
    display: block;
    width: 100%;
  }
  @media only screen and (max-width: 768px) {
    width: 70px;
  }
}

.header__title {
  padding: 0;
  margin: 0;
}

.icon-logo {
  height: 28px;
  width: 121px;
}

.header__title {
  margin-top: 3px;
  font-size: 13.1px;
  color: #888888;
}

.header__navbar {
  display: flex;
  align-items: center;
  color: #999999;
  transition: all 0.4s ease;
  z-index: 100;
  @media only screen and (max-width: 768px) {
    position: absolute;
    top: 0;
    right: -230px;
    flex-direction: column;
    align-items: flex-start;
    min-height: 100vh;
    width: 230px;
    padding: 25px;
    background-color: #28292d;
  }
  .icon {
    fill: #999999;
  }
}

.header__navbar.is-open {
  right: 0;
}

.header__notification {
  display: flex;
  align-items: center;
  padding-top: 10px;
  @media only screen and (max-width: 768px) {
    order: 2;
    flex-direction: column;
    align-items: flex-start;
    width: 100%;
    padding-top: 35px;
    padding-bottom: 23px;
    border-bottom: 1px solid #444444;
  }
}

.navbar__item {
  position: relative;
  display: flex;
  align-items: center;
  @media only screen and (max-width: 768px) {
    width: 100%;
    margin-bottom: 15px;
    margin-left: 0!important;
    padding-left: 0!important;
    .icon {
      margin-right: 5px;
    }
  }
  &:hover {
    color: #fff;
    .icon {
      fill: #fff;
    }
  }
}

.navbar__icon {
  padding: 0;
  margin: 0;
  @media only screen and (max-width: 768px) {
    width: 30px;
  }
}

.icon-blue-tv {
  width: 13px;
  height: 14px;
}

.icon-plus {
  font-size: 14px;
}

.icon-blue-list {
  width: 12px;
  height: 12px;
}

.icon-blue-comment {
  width: 15px;
  height: 12px;
}

.icon-blue-appointments {
  width: 24px;
  height: 18px;
}

.icon-blue-calls {
  width: 19px;
  height: 22px;
}

.icon-blue-journal {
  width: 20px;
  height: 14px;
}

.navbar__circle {
  position: absolute;
  text-align: center;
  align-items: center;
  justify-content: center;
  display: flex;
  width: 16px;
  height: 16px;
  font-size: 9.7px;
  font-weight: bold;
  color: #fff;
  border-radius: 50%;
  transition: all .2s ease;
  @media only screen and (max-width: 768px) {
    position: static;
    margin-left: auto;
    width: 21px;
    height: 21px;
    font-size: 13px;
  }
}

.navbar__circle--green {
  left: -8px;
  top: -7px;
  background-color: #9caa49;
  &:hover {
    background-color: lighten(#9caa49, 7%);
  }
}

.navbar__circle--red {
  left: -9px;
  top: -7px;
  background-color: #ff7e1b;
  &:hover {
    background-color: lighten(#ff7e1b, 7%);
  }
}

.navbar__circle--active {
  background-color: #5ec2ab;
}

.icon-email {
  font-size: 20px;
}

.navbar__inbox {
  margin-right: 17px;
  .navbar__text {
    @media only screen and (min-width: 768px) {
      margin-left: 7px;
    }
  }
}

.navbar__text {
  font-size: 13px;
  line-height: 1.2;
  letter-spacing: -0.3px;
}

.navbar__alert {
  @media only screen and (min-width: 768px) {
    margin-right: 26px;
  }
}

.icon-alert {
  width: 22px;
  height: 18px;
}

// navbar account
.navbar__account {
  display: flex;
  align-items: center;
  padding-left: 16px;
  border-left: 1px solid #444444;
  @media only screen and (max-width: 768px) {
    align-items: flex-start;
    border-left: none;
    margin-bottom: 0;
    padding-bottom: 45px;
    border-bottom: 1px solid #444444;
  }
}

.account__hello {
  font-size: 16px;
  line-height: 1.2;
  text-align: left;
  color: #9caa49;
  @media only screen and (max-width: 768px) {
    padding-top: 10px;
  }
}

.account__link {
  display: flex;
  align-items: center;
  @media only screen and (max-width: 768px) {
    position: absolute;
    bottom: 20px;
    left: 0;
    .icon {
      display: none;
    }
  }
  .navbar__text {
    margin-right: 6px;
    font-size: 11px;
    line-height: 1.5;
    font-weight: 500;
    letter-spacing: 0;
  }
}

.icon-settings {
  font-size: 15px;
  fill: #999999;
}

.navbar__menu {
  @media only screen and (min-width: 768px) {
    display: none;
  }
  order: 2;
  display: flex;
  flex-direction: column;
  width: 100%;
  padding-top: 30px;
  padding-bottom: 10px;
  border-bottom: 1px solid #444444;
  .icon {
    fill: #71b6cc;
  }
  .navbar__circle--active {
    display: none;
  }
  .navbar__circle--active {
    display: flex;
  }
}

// navbar logout
.navbar__logout {
  display: flex;
  align-items: center;
  margin-left: 22px;
  @media only screen and (min-width: 768px) {
    .navbar__text {
      display: none;
    }
  }
  @media only screen and (max-width: 768px) {
    width: 100%;
    order: 3;
    margin-left: 0;
    padding-top: 25px;
    .navbar__turn-on {
      display: flex;
      align-items: center;
      justify-content: flex-end;
      width: 100%;
      padding-bottom: 5px;
    }
    .navbar__text {
      padding-top: 2px;
      margin-right: 10px;
    }
  }
  .icon-turn-on {
    width: 21px;
    height: 24px;
  }
}

.navbar__logo-mini {
  @media only screen and (min-width: 768px) {
    display: none;
  }
  .icon {
    width: 32px;
    height: 33px;
  }
}

// MAIN
.main {
  position: relative;
  display: flex;
  min-height: calc(100vh - 74px);
  background-color: #fafafa;
}

// PRIMARY MENU
.main__navbar {
  position: relative;
  display: flex;
  padding-top: 45px;
  padding-bottom: 45px;
  color: #ececec;
  background-color: #5c5f69;
  transition: all 0.4s ease;
  &:hover {
    .secondmenu {
      width: 155px;
      visibility: visible;
    }
    .secondmenu-bg {
      width: 155px;
    }
  }
  &:before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    height: 100%;
    width: 42px;
    background-color: #3a3c44;
    z-index: 1;
  }
  .secondmenu-bg {
    position: absolute;
    top: 0;
    left: 42px;
    width: 0;
    height: 100%;
    background-color: #5c5f69;
    z-index: 1;
    transition: all 0.4s ease;
  }
}

.menu {
  z-index: 10;
}

.menu__item {
  padding-top: 2px;
  padding-bottom: 2px;
  position: relative;
  display: flex;
  height: 44px;
  transition: background-color 0.2s ease;
  &:hover {
    background-color: #9caa49;
    .secondmenu__title {
      color: #ebebeb;
      background-color: #9caa49;
    }
  }
}

.menu__icon {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 42px;
}

.icon-projects {
  width: 16px;
  height: 15px;
}

.icon-pencil {
  width: 20px;
  height: 19px;
}

.icon-reports {
  width: 17px;
  height: 18px;
}

.icon-agenda {
  width: 21px;
  height: 20px;
}

.icon-maps {
  width: 13px;
  height: 20px;
  fill: #dddddd;
}

.icon-schedule {
  width: 18px;
  height: 17px;
}

.secondmenu {
  position: absolute;
  top: 0;
  left: 42px;
  width: 0;
  overflow: hidden;
  padding-left: 0px;
  transition: all 0.2s linear;
  background-color: #5c5f69;
  transition: all 0.4s ease;
  &:hover {
    height: 300px;
  }
  &:hover .secondmenu__list {
    opacity: 1;
    display: flex;
    flex-direction: column;
    z-index: 1000;
  }
}

.secondmenu__title {
  position: relative;
  display: flex;
  align-items: center;
  height: 44px;
  padding-left: 10px;
  font-size: 14px;
  font-weight: 600;
  line-height: 1.57;
  z-index: 1;
  .chevron {
    position: absolute;
    top: 12px;
    left: 10px;
    display: inline-block;
  }
}

.secondmenu__list {
  position: absolute;
  top: 44px;
  left: 0;
  width: 100%;
  opacity: 0;
  transition: all 0.4s ease;
  z-index: 100;
}

.secondmenu__item {
  background-color: lighten(#5c5f69, 5%);
  &:first-child {
    padding-top: 10px;
  }
  &:last-child {
    padding-bottom: 10px;
  }
}

.secondmenu__link {
  display: block;
  padding: 5px 18px 5px 10px;
  font-size: 13px;
  white-space: nowrap;
  &:hover {
    color: #999933;
  }
}

// MAIN CONTENT
.main__content {
  width: 100%;
  margin-left: 16.5%;
  padding-bottom: 30px;
  @media only screen and (max-width: 1200px) {
    margin-left: 7%;
    padding-right: 2%;
  }
}

.content__title {
  margin-top: 98px;
  margin-bottom: 45px;
  @include rfs(22);
  font-weight: 500;
  color: #99aa46;
  @media only screen and (max-width: 768px) {
    margin-top: 60px;
    margin-bottom: 22px;
  }
  span {
    font-weight: 300;
    color: #999999;
  }
}

.content__head {
  margin-bottom: 85px;
  display: flex;
  align-items: center;
  font-size: 16px;
  @media only screen and (max-width: 768px) {
    margin-bottom: 30px;
    flex-wrap: wrap;
    align-items: flex-start;
  }
}

.content__box {
  padding-right: 27px;
  border-right: 1px solid #dddddd;
  @media only screen and (max-width: 480px) {
    padding-right: 0;
    border: none;
  }
}

.content__hello {
  @media only screen and (max-width: 480px) {
    margin-top: 10px;
  }
}

.content__name {
  font-weight: 600;
  color: #9caa49;
}

.content__text {
  font-size: 16px;
  color: #5c5f69;
}

.weather {
  display: flex;
  align-items: center;
  padding-left: 27px;
  @media only screen and (max-width: 480px) {
    margin-top: 10px;
    padding-left: 50px;
  }
}

.weather__box {
  line-height: 1;
  letter-spacing: 0.3px;
}

.weather__date {
  font-size: 14.8px;
  color: #9baa48;
}

.weather__forecast {
  font-size: 16.3px;
  color: #aaaaaa;
}

.weather__icon {
  margin-left: 13px;
}

.icon-weather {
  width: 44px;
  height: 32px;
}

// content blocks
.content__blocks {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 1010px;
  max-width: 1010px;
  @media only screen and (max-width: 1366px) {
    width: 100%;
  }
}

.blocks__item {
  flex-basis: 32.2%;
  max-width: 330px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 189px;
  margin-bottom: 13px;
  padding: 5.7%;
  background-color: #f2f2f2;
  @media only screen and (max-width: 768px) {
    flex-basis: 49%;
    align-items: center;
    height: 130px;
  }
  @media only screen and (max-width: 480px) {
    flex-basis: 100%;
  }
}

.block__link {
  text-align: center;
}

.icon-projects-big {
  width: 43px;
  height: 40px;
}

.icon-agenda-big {
  width: 41px;
  height: 39px;
}

.icon-schedule-big {
  width: 40px;
  height: 38px;
}

.icon-pencil-big {
  width: 43px;
  height: 39px;
}

.icon-reports-big {
  width: 36px;
  height: 38px;
}

.icon-maps-big {
  width: 25px;
  height: 40px;
  fill: #5ec2ab;
}

.block__name {
  margin-top: 15px;
  @include rfs(20);
  font-weight: 300;
  color: #5c5f69;
  @media only screen and (min-width: 1200px) {
    white-space: nowrap;
  }
}

// FOOTER 
.content__footer {
  display: flex;
  align-items: baseline;
  margin-top: 5%;
  padding-bottom: 15px;
  @media only screen and (max-width: 480px) {
    flex-wrap: wrap;
    justify-content: center;
  }
  .footer__logo-link {
    display: inline-block;
    margin-right: 12px;
    img {
      display: block;
      width: 98px;
      height: 23px;
    }
  }
  .footer__title {
    font-size: 13.1px;
    color: #cfd0d6;
  }
}


/******************
Modal: New Message
******************/

.page-message {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.8);
}

.message-wrap {
  max-width: 862px;
  width: 862px;
  padding: 17px 16px 37px;
  background-color: #fff;
  @media only screen and (max-width: 480px) {
    max-width: 100%;
    padding: 0%;
  }
  button {
    background-color: transparent;
    border: none;
    &:hover {
      cursor: pointer;
    }
  }
  .message__head {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 41px;
    margin-bottom: 20px;
    padding-left: 22px;
    padding-right: 18px;
    color: #fff;
    background-color: #71b6cc;
  }
  .message__title {
    font-size: 16.6px;
  }
  .message__close {
    display: flex;
    align-items: center;
    font-family: $font-primary;
    font-size: 12px;
    font-weight: 600;
    color: #fff;
    line-height: 1.2;
    text-align: center;
    .icon {
      margin-left: 3px;
      fill: #fff;
      font-size: 17px;
    }
  }
  .message__body {
    padding-left: 20px;
    padding-right: 20px;
    @media only screen and (max-width: 480px) {
      padding-left: 2%;
      padding-right: 2%;
    }
  }
  // custom select
  /* Reset Select */
  select {
    -webkit-appearance: none;
    -moz-appearance: none;
    -ms-appearance: none;
    appearance: none;
    outline: 0;
    box-shadow: none;
    border: 0 !important;
    background: #fff;
    background-image: none;
  }
  /* Custom Select */
  .table__customselect {
    flex-grow: 1;
  }
  .select {
    position: relative;
    display: block;
    width: 100%;
    color: #666666;
    user-select: none;
    -webkit-touch-callout: none;
    .placeholder {
      position: relative;
      display: block;
      height: 100%;
      line-height: 41px;
      z-index: 2;
      font-size: 13px;
      padding-right: 15px;
      cursor: pointer;
      &:after {
        position: absolute;
        right: 11px;
        top: 50%;
        transform: translateY(-50%);
        color: #aaaaaa;
        font-family: 'FontAwesome';
        content: '\f0d7';
        z-index: 10;
      }
    }
    &.is-open {
      .placeholder:after {
        content: '\f0d8';
      }
      ul {
        display: block;
      }
    }
    ul {
      display: none;
      position: absolute;
      width: 100%;
      top: 100%;
      left: 0;
      list-style: none;
      margin: 0;
      padding: 0;
      z-index: 3;
      background-color: rgba(238, 238, 238, 0.9);
      li {
        display: block;
        padding-left: 15px;
        font-size: 13px;
        line-height: 2;
        cursor: pointer;
        &:hover {
          background: #71b6cc;
          color: #fff;
        }
      }
    }
  }
  // end custom select
  .message__input-row {
    display: flex;
    align-items: center;
    height: 41px;
    padding-top: 2px;
    border-bottom: 0.5px solid #eeeeee;
    input {
      flex-grow: 1;
      height: 98%;
      font-size: 13px;
      color: #666666;
      border: none;
      &:focus {
        outline: none;
      }
    }
  }
  .input-row__caption {
    width: 70px;
    margin-right: 14px;
    font-size: 14.8px;
    text-align: right;
    color: #aaaaaa;
    @media only screen and (max-width: 480px) {
      width: auto;
    }
  }
  .select__contact {
    margin-left: 19px;
    font-size: 14.8px;
    color: #aaaaaa;
    &:hover {
      cursor: pointer;
    }
  }
  #messageRowCc,
  #messageRowBcc {
    display: none;
  }
  .message__textarea {
    width: 100%;
    margin-top: -1px;
    margin-bottom: 0;
    padding: 5% 4%;
    color: #666666;
    border-color: #eeeeee;
    border-bottom: none;
    resize: none;
    &:focus {
      outline: none;
    }
  }
  textarea::placeholder {
    font-family: $font-primary;
    font-size: 13px;
    color: #666666;
  }
  .message__footer {
    margin-top: -4px;
    min-height: 62px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: #eeeeee;
  }
  .message__editor {
    padding-left: 22px;
    font-family: Verdana;
    font-size: 10px;
    color: #666666;
  }
  .message__buttons {
    display: flex;
    align-items: center;
    .icon {
      font-size: 18px;
      fill: #666666;
      &:hover {
        fill: #333;
      }
    }
  }
  .message__button {
    margin-left: 3px;
  }
  .message__submit {
    height: 32px;
    width: 32px;
    padding-left: 8px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 17px;
    margin-left: 17px;
    border-radius: 50%;
    background-color: #66cccc;
    transition: all 0.2s ease;
    &:hover {
      background-color: darken(#66cccc, 10%)
    }
    .icon {
      font-size: 18px;
      fill: #fff;
    }
  }
}

// end NEW MESSAGE

/******************
     CALENDAR
******************/

.main__calendar {
  flex-grow: 1;
  .calendar__head {
    padding-top: 10px;
    padding-bottom: 40px;
    background-color: lighten(#28292d, 5%);
  }
  .calendar__topnav {
    display: flex;
    justify-content: space-between;
    padding-left: 18px;
    padding-right: 30px;
    color: #bbb;
    .icon {
      fill: #bbb;
    }
  }
  .calendar__back {
    display: flex;
    align-items: center;
    &:hover {
      color: #fff;
      .icon {
        fill: #fff;
      }
      .icon-back {
        border-color: #fff;
      }
    }
  }
  .icon-back {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 21px;
    height: 21px;
    margin-right: 8px;
    border: 1px solid #bbb;
    border-radius: 50%;
  }
  .icon-menu {
    font-size: 21px;
    &:hover {
      fill: #fff;
    }
  }
  .calendar__carousel {
    display: flex;
    flex-direction: column;
    align-items: center;
    @include rfs(24.8);
    color: #dddddd;
    @media only screen and (max-width: 480px) {
      font-size: 18px;
    }
  }
  .icon-calendar {
    margin-top: 0.7em;
    margin-bottom: 0.7em;
    @include rfs(47);
    fill: #ddd;
  }
  .calendar__date-wrap {
