/****************
   MOBILE TABLE
 ***************/

.m-table {
  max-width: 768px;
  width: 100%;
  margin: 0 auto;
  color: #5c5f69;
  .m-table__head {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    min-height: 64px;
    padding: 3% 2% 2%;
    background-color: #f2f2f2;
  }
  .m-table__heading {
    display: flex;
    align-items: center;
    white-space: nowrap;
    .heading__icon {
      margin-right: 8px;
    }
  }
  .heading__title {
    font-size: 13px;
    font-weight: 600;
    letter-spacing: -0.2px;
    color: #9da2aa;
  }
  .m-table__actions {
    position: relative;
  }
  .actions__inner {
    display: flex;
    align-items: center;
    cursor: pointer;
  }
  .actions__name {
    display: flex;
    flex-direction: column;
  }
  .btn__sort {
    font-size: 17px;
    text-transform: uppercase;
    text-align: center;
    color: #5c5f69;
  }
  .btn__caption {
    margin-top: 5px;
    font-size: 12px;
    font-weight: bold;
    text-align: center;
    color: #71b6cc;
  }
  .chevron-right {
    margin-left: 30px;
    .icon {
      fill: #3a3c44;
      font-size: 16px;
    }
  }
  // actions conxext menu
  .actions__menu {
    display: none;
    position: absolute;
    right: -15px;
    top: calc(100% + 14px);
    width: 174px;
  }
  .actions__menu-item {
    display: flex;
    align-items: center;
    height: 37px;
    background-color: #404249;
    box-shadow: 1px 1.7px 7px 0 rgba(0, 0, 0, 0.26);
    cursor: pointer;
    &:nth-child(odd) {
      background-color: #3a3c44;
    }
    .chevron-right2 {
      width: 35px;
      padding-left: 5px;
      text-align: center;
      visibility: hidden;
      .icon {
        width: 6px;
        height: 12px;
        fill: #cccccc;
      }
    }
  }
  .actions__name {
    font-size: 12px;
    font-weight: 600;
    color: #aaaaaa;
    &:hover {
      color: #71b6cc;
    }
  }
  .item--is-active {
    .chevron-right2 {
      visibility: visible;
    }
    .actions__name {
      color: #71b6cc;
    }
  }
  // m-table body
  .m-table__body {
    padding: 2%;
  }
  .m-table__block {
    margin-bottom: 17px;
  }
  .block__header {
    display: flex;
    align-items: center;
    justify-content: space-around;
    height: 50px;
    background-color: #eeeeee;
    @media only screen and (max-width: 480px) {
      justify-content: space-between;
      padding: 0 3%;
    }
  }
  .block__act {
    display: flex;
    align-items: center;
    color: #9da2aa;
    &:hover {
      color: #71b6cc;
      .icon {
        fill: #71b6cc;
      }
    }
  }
  .act__name {
    font-size: 11px;
    text-transform: uppercase;
    font-weight: 700;
  }
  .act__icon {
    margin-right: 8px;
    .icon {
      fill: #9da2aa;
    }
    .icon-tick {
      font-size: 22px;
    }
    .icon-phone {
      font-size: 20px;
    }
    .icon-paper-plane {
      font-size: 20px;
    }
    .icon-close {
      font-size: 16px;
    }
  }
  .block__info {
    padding: 20px 10px 20px 15px;
    font-size: 13px;
    line-height: 1.2;
  }
  .block--green {
    background-color: #e4e7d1;
    .info__cirlce {
      background-color: #a9b366;
    }
  }
  .block--tan {
    background-color: #fad6c9;
    .info__cirlce {
      background-color: #ff7e1b;
    }
  }
  .block__row {
    display: flex;
    min-height: 24px;
    align-items: center;
    &:first-child {
      margin-bottom: 5px;
    }
  }
  .info__client {
    font-size: 18px;
    font-weight: 500;
  }
  .info__cirlce {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 21px;
    height: 21px;
    margin-left: auto;
    font-size: 13px;
    font-weight: bold;
    color: #fff;
    border-radius: 50%;
  }
  .info__name {
    min-width: 70px;
    font-weight: bold;
  }
  .content__footer {
    display: flex;
    justify-content: center;
  }
}

//  end mobile table

/*begin hamburger*/

#nav-icon1 {
  width: 18px;
  height: 18px;
  position: relative;
  -webkit-transform: rotate(0deg);
  -moz-transform: rotate(0deg);
  -o-transform: rotate(0deg);
  transform: rotate(0deg);
  -webkit-transition: 0.5s ease-in-out;
  -moz-transition: 0.5s ease-in-out;
  -o-transition: 0.5s ease-in-out;
  transition: 0.5s ease-in-out;
  cursor: pointer;
}

#nav-icon1 span {
  display: block;
  position: absolute;
  height: 2px;
  width: 100%;
  background: $grey;
  border-radius: 9px;
  opacity: 1;
  left: 0;
  -webkit-transform: rotate(0deg);
  -moz-transform: rotate(0deg);
  -o-transform: rotate(0deg);
  transform: rotate(0deg);
  -webkit-transition: 0.25s ease-in-out;
  -moz-transition: 0.25s ease-in-out;
  -o-transition: 0.25s ease-in-out;
  transition: 0.25s ease-in-out;
}

#nav-icon1 span:nth-child(1) {
  top: 0;
}

#nav-icon1 span:nth-child(2) {
  top: 6px;
}

#nav-icon1 span:nth-child(3) {
  top: 12px;
}

#nav-icon1.open span:nth-child(1) {
  top: 0px;
  -webkit-transform: rotate(135deg);
  -moz-transform: rotate(135deg);
  -o-transform: rotate(135deg);
  transform: rotate(135deg);
}

#nav-icon1.open span:nth-child(2) {
  opacity: 0;
  left: -60px;
}

#nav-icon1.open span:nth-child(3) {
  top: 0px;
  -webkit-transform: rotate(-135deg);
  -moz-transform: rotate(-135deg);
  -o-transform: rotate(-135deg);
  transform: rotate(-135deg);
}

@media only screen and (min-width: 769px) {
  .header {
    #touch-menu {
      display: none!important;
    }
  }
}

@media only screen and (max-width: 768px) {
  .header {
    #nav-wrap {
      display: none;
    }
    #touch-menu {
      display: inline-block;
      z-index: 100;
    }
  }
}


/*end hamburger*/

.preloader {
  height: 100%;
  width: 100%;
  background: rgba(34, 34, 34, 0.8);
  position: fixed;
  left: 0;
  top: 0;
  z-index: 10000;
  transition: 0.5s all;
  opacity: 1;
}

.preloader.fade {
  opacity: 0;
}

.scene {
  width: 100%;
  height: 100%;
  min-height: 100vh;
  perspective: 600;
  display: flex;
  align-items: center;
  justify-content: center;
  svg {
    width: 80px;
    height: 100px;
    background: url(data:image/png;base64,);
    background-size: 25px 25px;
    background-repeat: no-repeat;
    background-position: center 40%;
  }
}